<!-- eslint-disable vue/no-unused-components -->
<template>
    <div class="bigbox">
        <div class="title">项目申报系统</div>
        <el-card class="box-card" style="overflow-y:auto;">
            <div style="position:relative;top:-30px">
                <div style="position:relative;left:90%;top: -5;" class="username">
                    {{ name }}
                    <el-avatar size="small" style="margin-left:5px;position:relative;top: 5px;"
                        src="https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png">
                    </el-avatar>
                </div>
                <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                    <el-menu-item index="developing">进行中</el-menu-item>
                    <el-menu-item index="developed">已完成</el-menu-item>
                </el-menu>
            </div>
            <div style="overflow:auto;">
                <developing v-show="show1"></developing>
                <developed v-show="show2"></developed>
            </div>

        </el-card>
    </div>
</template>
<script>
import developed from "./Developed.vue";
import developing from "./Developing.vue";
export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: 'home',
    components: {
        // eslint-disable-next-line vue/no-unused-components
        developed,
        // eslint-disable-next-line vue/no-unused-components
        developing

    },
    data() {
        return {
            name: localStorage.getItem('user_name'),
            activeIndex: 'developing',
            account: '',
            password: '',
            show1: true,
            show2: false
        }
    },
    beforeCreate() {
        document.querySelector('body').setAttribute('style', 'margin:0;')
    },
    methods: {
        // 切换菜单
        handleSelect(key, keyPath) {
            if (keyPath == 'developing') {
                this.show1 = true
                this.show2 = false
            }
            else if (keyPath == 'developed') {
                this.show2 = true
                this.show1 = false
            }
        }
    }
}

</script>
<style scoped>
.username {
    font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
    font-size: 20px;
    color: rgb(118, 114, 114);
}

.title {
    width: 100%;
    color: #fff;
    position: absolute;
    text-align: center;
    top: 5%;
    font-size: 40px;
    font-weight: 600;
}

.bigbox {

    position: fixed;
    height: 100%;
    width: 100%;
    background-color: #3d789f !important;
}

.text {
    font-size: 14px;
}

.item {
    padding: 18px 0;
}

.box-card {
    width: 480px;
    position: absolute;
    left: 20%;
    top: 38%;
    width: 80%;
    height: 65%;
    margin: -150px 0 0 -190px;
    padding: 40px;
    border-radius: 5px;
    background: #fff;
}

.login {
    position: relative;
    top: -30px;
    text-align: center;
    font-size: 30px;
    color: #4883ab;
    font-weight: 600;
}
</style>