<!-- eslint-disable vue/multi-word-component-names -->
<template>
    <div style="overflow:auto;">
        <template>
            <i class="el-icon-search"></i>
            <el-input v-model="searchProject" placeholder="请输入搜索的项目" style="width:250px;margin-left: 10px;"></el-input>
            <br>
            <el-table :data="projectSearch(searchProject)" style="width: 100%" :default-expand-all="role">
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand" label-width="150px">
                            <el-form-item label="项目名称">
                                <span>{{ props.row.projectName }}</span>
                            </el-form-item>
                            <el-form-item label="标段名称">
                                <span>{{ props.row.sectionName }}</span>
                            </el-form-item>
                            <el-form-item label="项目业主">
                                <span>{{ props.row.projectOwner }}</span>
                            </el-form-item>
                            <el-form-item label="委托单位">
                                <span>{{ props.row.clientUnit }}</span>
                            </el-form-item>
                            <el-form-item label="编制机构">
                                <span>{{ props.row.compileInstitution }}</span>
                            </el-form-item>
                            <el-form-item label="评审类别">
                                <span>{{ props.row.reviewSort }}</span>
                            </el-form-item>
                            <el-form-item label="清单规范">
                                <span>{{ props.row.inventoryNorm }}</span>
                            </el-form-item>
                            <el-form-item label="定额体系">
                                <span>{{ props.row.quotaSystem }}</span>
                            </el-form-item>
                            <el-form-item label="立项时间">
                                <span>{{ props.row.approvalTime }}</span>
                            </el-form-item>
                            <el-form-item label="项目地址">
                                <span>{{ props.row.projectAddress }}</span>
                            </el-form-item>
                            <el-form-item label="立项文号">
                                <span>{{ props.row.approvalNumber }}</span>
                            </el-form-item>
                            <el-form-item label="项目联系人">
                                <span>{{ props.row.username }}</span>
                            </el-form-item>
                            <el-form-item label="委托联系人">
                                <span>{{ props.row.clientUsername }}</span>
                            </el-form-item>
                            <el-form-item label="组织机构代码">
                                <span>{{ props.row.institutionNumber }}</span>
                            </el-form-item>
                            <el-form-item label="行业类别">
                                <span>{{ props.row.industrySort }}</span>
                            </el-form-item>
                            <el-form-item label="价格依据">
                                <span>{{ props.row.priceGist }}</span>
                            </el-form-item>
                            <el-form-item label="立项金额">
                                <span>{{ props.row.approvalPrice }}</span>
                            </el-form-item>
                            <el-form-item label="技术评审意见编号">
                                <span>{{ props.row.technicalNumber }}</span>
                            </el-form-item>
                            <el-form-item label="项目联系人电话">
                                <span>{{ props.row.userNumber }}</span>
                            </el-form-item>
                            <el-form-item label="委托联系人电话">
                                <span>{{ props.row.clientNumber }}</span>
                            </el-form-item>
                            <el-form-item label="编制人">
                                <span>{{ props.row.compileName }}</span>
                            </el-form-item>
                            <el-form-item label="送审投资额">
                                <span>{{ props.row.investPrice }}</span>
                            </el-form-item><br>
                            <el-form-item label="送审投资额">
                                <span>{{ props.row.investPrice }}</span>
                            </el-form-item>
                            <el-form-item label="评价" style="width:100%;color: cornflowerblue;">
                                <span>{{ props.row.suggestion }}</span>
                            </el-form-item>
                            <el-form-item label="项目基本情况" style="width:100%">
                                <span>{{ props.row.projectCondition }}</span>
                            </el-form-item><br>
                            <div v-show="!role">
                                <template>
                                    <el-radio v-model="state" label="1">通过</el-radio>
                                    <el-radio v-model="state" label="-1">退回</el-radio>
                                </template>
                                <el-input type="textarea" :rows="2" placeholder="请输入内容" v-model="appraise"
                                    style="margin-top:10px;margin-bottom:10px">
                                </el-input><br>
                                <el-button size="medium" @click="answer(props.row.id)">评价</el-button>
                            </div>

                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column label="项目名称" prop="projectName" width="400px">
                </el-table-column>
                <el-table-column label="项目业主" prop="projectOwner" width="400px">
                </el-table-column>
                <el-table-column label="承办人" prop="username" width="200px">
                </el-table-column>
            </el-table>
        </template>
    </div>
</template>

<script>
export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: 'developed',
    data() {
        return {
            allProject: [],
            searchProject: '',
            appraise: '',
            // 代表项目的通过或者退回
            state: 0,
            url: localStorage.getItem('role') == '1111' ? 'users/my_project' : '/admins/all',
            role: localStorage.getItem('role') == '1111'
        }
    },
    created() {
        console.log(this);
        this.$axiosGet(this.url).then(res => {
            console.log(res);
            this.allProject = res
            /*        localStorage.setItem("token", res.token); */
        })
    },
    methods: {
        /* 搜索相关项目 */
        projectSearch(keywords) {
            var searchResult = []
            this.allProject.forEach((item) => {
                if (item.projectName.indexOf(keywords) != -1) {
                    searchResult.push(item)
                }
            })
            return searchResult
        },
        // 评价这个项目
        answer(id) {
            if (this.state == 0)
                this.$message.error('请选择通过或退回')
            else {
                this.$axiosPost('/admins/do_project', {
                    id: id,
                    suggestion: this.appraise,
                    state: this.state
                }).then(res => {
                    console.log(res);
                })
            }

        }
    }
}
</script>

<style>
.demo-table-expand {
    font-size: 0;
}

.demo-table-expand label {
    width: 90px;
    color: #99a9bf;
}

.demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
}
</style>