<template>
    <div class="bigbox">
        <div class="title">项目申报系统</div>
        <el-card class="box-card">
            <div class="login">Login</div><br>
            <div style="position:relative;top:-30px">
                <el-input placeholder="请输入账号" v-model="account" clearable></el-input><br><br>
                <el-input placeholder="请输入密码" v-model="password" show-password></el-input><br><br>
                <el-button type="primary" plain style="width:260px" @click="go()">登录</el-button>
            </div>

        </el-card>
    </div>
</template>
<script>
export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name: 'login',
    data() {
        return {
            account: '',
            password: ''
        }
    },
    created() {
    },
    beforeCreate() {
        document.querySelector('body').setAttribute('style', 'margin:0;')
    },
    methods: {
        go() {
            this.$axiosPost('/login', {
                username: this.account,
                password: this.password
            }).then(res => {
                console.log(res);
                localStorage.setItem("token", res.token);
                localStorage.setItem('role', res.data.role)
                localStorage.setItem('user_name', res.data.name)
                if (res.message == "登录成功")
                    this.$router.push('/home')
            })
        }
    }
}

</script>
<style scoped>
.title {
    width: 100%;
    color: #fff;
    position: absolute;
    text-align: center;
    top: 20%;
    font-size: 40px;
    font-weight: 600;
}

.bigbox {

    position: fixed;
    height: 100%;
    width: 100%;
    background-color: #3d789f !important;
}

.text {
    font-size: 14px;
}

.item {
    padding: 18px 0;
}

.box-card {
    width: 480px;
    position: absolute;
    left: 50%;
    top: 50%;
    width: 300px;
    height: 220px;
    margin: -150px 0 0 -190px;
    padding: 40px;
    border-radius: 5px;
    background: #fff;
}

.login {
    position: relative;
    top: -30px;
    text-align: center;
    font-size: 30px;
    color: #4883ab;
    font-weight: 600;
}
</style>